<template>
  <div class="max flex-center">
    <span class="welcome" :class="{ showWelcome: showSpan}">Jyochinyo</span>
  </div>
</template>

<script>
export default {
  data () {
    return {
      showSpan: false
    }
  },
  created () {
    setTimeout(() => {
      this.showSpan = true
      setTimeout(() => {
        this.$router.push({path: '/homePage'})
      }, 1500)
    }, 500)
  }
}
</script>

<style lang="less" scoped>
@import "~common/style/variable.less";

.max {
  height: 100vh;
  width: 100vw;
}
.welcome {
  color: rgb(62, 22, 36);
  font-size: 3em;
  line-height: 1.5em;
  transform: scale(1.4,1.4);
  opacity: 0;
}

.showWelcome {
  transition: all 1.5s;
  transform: scale(1,1);
  opacity: 1;
}
</style>


